import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';

import { WeatherInfoComponent } from './containers';

@NgModule({
    imports: [
        BrowserModule
    ],
    declarations: [
        WeatherInfoComponent
    ],
    bootstrap: [WeatherInfoComponent]
})
export class AppModule { }


